import React, { useState } from 'react'
import SuperSelect from './common/c5-SuperSelect/SuperSelect'
import SuperRadio from './common/c6-SuperRadio/SuperRadio'
import s2 from '../../s1-main/App.module.css'
import s from './HW7.module.css'

//   OptionType
type OptionType = { id: string | number; value: string };

//   
const arr: OptionType[] = [
    { id: 1, value: 'x' },
    { id: 2, value: 'y' },
    { id: 3, value: 'z' },
];

//  HW7
const HW7 = () => {
    const [value, onChangeOption] = useState<OptionType | null>(arr[0]); //      

    return (
        <div id="hw7">
            <div className={s2.hwTitle}>Homework #7</div> {/*    App.module.css */}

            {/*  : */}
            <div className={s2.hw}>
                <div className={s.container}> {/*    HW7.module.css */}
                    <div>
                        <SuperSelect
                            id="hw7-super-select" //  id
                            options={arr}
                            value={value?.id} //    id
                            onChangeOption={(option) => onChangeOption(option)} //  
                        />
                    </div>
                    <div>
                        <SuperRadio
                            id="hw7-super-radio" //  id
                            name="hw7-radio"
                            options={arr}
                            value={value?.id} //    id
                            onChangeOption={(option) => onChangeOption(option)} //  
                        />
                    </div>
                </div>
            </div>
        </div>
    );
};

export default HW7;